.container-grid {
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    width: 98%;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.border{    
    border: 2px solid rgb(253, 253, 253);
    border-radius: 12px;
    margin:0.3em;
}

.row-buttons{
    display: inline-block;
}

.row-buttons-menu{
    display: -webkit-inline-flex;
    display: inline-flex;
    white-space: nowrap;
    text-align: center;
}

.btn{
    margin: 0.5em;
}

.toolbar-item {
    margin: 0.5em;
}

.wj-flexgridsearch {
    width: 100%;
    margin: 0.5em;
}

.wj-flexgrid {
    height: 75%;
}

.wj-flexgrid .wj-cell {
    padding: 7px;
    border: none;
}

.wj-cell.wj-state-invalid:not(.wj-header)::after {
    top: -14px;
    border: 14px solid transparent;
    border-right-color: red;
}

.flag-icon {
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.4);
}

.color-tile {
    display: inline-block;
    position: relative;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.4);
    vertical-align: middle;
}

.change-up {
    color: darkgreen;
}

.change-up:after {
    content: '\25b2';
    margin-left: 0.5em;
}

.change-down {
    color: darkred;
}

.change-down:after {
    content: '\25bc';
    margin-left: 0.5em;
}

.cell-rating {
    font-size: 12px;
}

.wj-flexgrid .wj-detail {
    padding: 4px 16px;
}

.wj-detail h3 {
    margin: 10px 0;
}
.modal-body {
    min-width: 300px;
}

input:invalid {
    border-color: red;
}

